

.container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #e8e8e8;
}


.main {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #001eff;
  max-height: 500px;
  width: 400px;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: rgba(55, 0, 255, 0.442) 0px 30px 90px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 14px;
  padding: 24px;
}

/*checkbox to switch from sign up to login*/

#chk {
  display: none;
}

/*Login*/

.login {
  position: relative;
  width: 100%;
  height: 100%;
}

.login label {
  margin: 5% 0 5%;
}

label {
  color: #a8a8a8;
  font-size: 2rem;
  justify-content: center;
  display: flex;
  font-weight: bold;
  cursor: pointer;
  transition: .5s ease-in-out;
}

.input {
  width: 100%;
  height: 40px;
  font-size: 1rem;
  background: #b0b0b0;
  padding: 10px;
  margin-top: 15px;
  border: none;
  outline: none;
  border-radius: 4px;
}

/*Register*/

.register {
  background: #eee;
  border-radius: 60% / 10%;
  transition: .8s ease-in-out;
}

.register label {
  color: #573b8a;
  transform: scale(.6);
}

#chk:checked ~ .register {
  transform: translateY(-68%);
}

#chk:checked ~ .register label {
  transform: scale(1);
  margin-bottom: .5rem;
}

#chk:checked ~ .login label {
  transform: scale(.6);
}   

/*Button*/

.form button {
  width: 70%;
  height: 40px;
  margin: 15px auto 10%;
  color: #fff;
  background: #573b8a;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: .2s ease-in;
}

.form button:hover {
  background-color: #6d44b8;
}